import React, { useCallback, useMemo } from 'react';
import { View, ScrollView, FlatList, StatusBar } from 'react-native';
import { Button, SCREEN_WIDTH, Text } from 'tradewize-component';
import { styles } from './styles';
import { IconSearch, IconWarning } from '../../assets/svg';
import { colors } from '../../theme';
import { SvgXml } from 'react-native-svg';

import { ITEM_HOME } from './model.ts';
import CardItem from './components/CardItem';
import HeaderComponent from './components/Header/index.tsx';
import { useHome } from '../../hooks/useHome.ts';
import FastImage from 'react-native-fast-image';
import { homeBanner } from '../../assets/images/index.ts';
import JobsToday from './components/JobsToday/index.tsx';
import { useSafeAreaInsets } from 'react-native-safe-area-context';
import { useAppTranslation } from '../../hooks/useAppTranslation.ts';
import { useDispatch, useSelector } from 'react-redux';
import { logger } from '../../utils/index.tsx';
import ImportantThings from './components/ImportantThings/index.tsx';
import { AutoSkeletonView } from 'react-native-auto-skeleton';

const HomeScreen = () => {
  const dispatch = useDispatch();
  const { t } = useAppTranslation();
  const insets = useSafeAreaInsets();
  const importantJobs = useSelector((state: any) => state.home.importantJobs);

  useHome();

  const handleOpenImportantJobs = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(
        redux.actionContainer.showCustomModal({
          isBackdropPress: false,
          animationIn: 'zoomIn',
          animationOut: 'zoomOut',
          animationInTiming: 400,
          animationOutTiming: 400,
          children: React.createElement(ImportantThings),
        }),
      );
    } catch (error) {
      logger('error handleOpenImportantJobs', error);
    }
  }, [dispatch]);

  const renderImportantJobs = useMemo(() => {
    if (importantJobs?.isLoading) {
      return (
        <AutoSkeletonView isLoading={importantJobs?.isLoading}>
          <Button
            onPress={handleOpenImportantJobs}
            variant="ghost"
            style={styles.importantJobsContainer}
          >
            <View style={styles.importantJobsWarningContainer}>
              <SvgXml
                xml={IconWarning()}
                width={SCREEN_WIDTH * 0.06}
                height={SCREEN_WIDTH * 0.06}
              />
            </View>
          </Button>
        </AutoSkeletonView>
      );
    }

    if (importantJobs?.data?.totalJobs > 0 && !importantJobs?.isLoading) {
      return (
        <Button
          onPress={handleOpenImportantJobs}
          variant="ghost"
          style={styles.importantJobsContainer}
        >
          <View style={styles.importantJobsWarningContainer}>
            <SvgXml
              xml={IconWarning()}
              width={SCREEN_WIDTH * 0.06}
              height={SCREEN_WIDTH * 0.06}
            />
            <Text style={styles.importantJobsWarningText}>
              {importantJobs?.data?.totalJobs} {t('IMPORTANT_JOBS')}
            </Text>
          </View>
        </Button>
      );
    }

    return null;
  }, [
    handleOpenImportantJobs,
    importantJobs?.data?.totalJobs,
    importantJobs?.isLoading,
    t,
  ]);

  return (
    <View style={[styles.container, { paddingTop: insets.top }]}>
      <HeaderComponent />
      <StatusBar
        backgroundColor={colors.mainColorPrimary}
        barStyle="light-content"
      />

      <Button style={styles.findJobContainer}>
        <Text style={styles.txtFindJob}>
          {t('ARE_YOU_LOOKING_FOR_CONTRUCTION_TODAY')}
        </Text>
        <Button
          fullWidth
          variant="ghost"
          disabled
          style={styles.searchContainer}
          leftIcon={
            <SvgXml
              xml={IconSearch(colors.grayScale70)}
              width={SCREEN_WIDTH * 0.06}
              height={SCREEN_WIDTH * 0.06}
            />
          }
          title={t('SEARCH_JOBS')}
          textStyle={styles.placeholder}
        />
      </Button>

      <ScrollView style={styles.bodyContainer}>
        <JobsToday />

        {renderImportantJobs}

        <FlatList
          scrollEnabled={false}
          style={styles.flatList}
          contentContainerStyle={styles.flatListContentContainer}
          numColumns={4}
          data={ITEM_HOME}
          renderItem={({ item }) => <CardItem item={item} />}
          keyExtractor={(_item, index) => index.toString()}
        />

        <FastImage
          source={homeBanner}
          style={styles.homeBanner}
          resizeMode={FastImage.resizeMode.contain}
        />
      </ScrollView>
    </View>
  );
};

export default HomeScreen;
