import { useFocusEffect } from '@react-navigation/native';
import React, { useCallback, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { logger } from '../utils';
import { UserProfile } from '../types/profile';
import { useAppTranslation } from '../hooks/useAppTranslation';
import { navigationRef } from '../services/NavigationService';
import { SCREEN_ENUM } from '../types/global';
import { updateProfile } from '../helper';
import DueVideo from '../screens/Home/components/DueVideo';
import ImportantThings from '../screens/Home/components/ImportantThings';
interface HomeMedia {
  key: string;
  url: { [key: string]: string };
  subtitle: { [key: string]: string };
}

export const useHome = () => {
  const dispatch = useDispatch();
  const { t } = useAppTranslation();

  const userProfile: UserProfile = useSelector(
    (state: any) => state.auth.userProfile,
  );
  const dataMedia: { [key: string]: HomeMedia } | null = useSelector(
    (state: any) => state.app.dataMedia,
  );
  const importantJobs = useSelector(
    (state: any) => state.home.importantJobs.data,
  );
  const myJobsToday = useSelector((state: any) => state.home.myJobsToday?.data);
  const isShowDueVideo = useSelector((state: any) => state.home.isShowDueVideo);

  const fetchGetMyTodayJobs = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer.getMyJobsTodayAsync());
    } catch (error) {
      logger('error fetchGetMyTodayJobs', error);
    }
  }, [dispatch]);

  const fetchGetImportantJobs = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer.getImportantJobsAsync());
    } catch (error) {
      logger('error fetchGetImportantJobs', error);
    }
  }, [dispatch]);

  const fetchStatusAccount = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      if (!userProfile || importantJobs?.isLoading || myJobsToday?.isLoading) {
        return;
      }

      //1. check if user is first reset password
      if (!userProfile?.isFirstResetPassword) {
        dispatch(
          redux.actionContainer.showAlert({
            isBackdropPress: false,
            title: t('CHANGE_PASSWORD'),
            message: t('FIRST_TIME_PASSWORD_CHANGE_MESSAGE'),
            type: 'warning',
            buttonText: t('CHANGE_NOW'),
            onClose: () => {
              redux.actionContainer?.hideAlert();
              navigationRef.navigate(SCREEN_ENUM.ChangePasswordScreen);
            },
          }),
        );
        return;
      }

      //2. if open video training first time
      if (
        !userProfile?.isWatchedTrainingVideo &&
        userProfile?.isFirstResetPassword
      ) {
        dispatch(
          redux.actionContainer.showVideoModal({
            source: dataMedia?.['3_ONBOARDING_VIDEO']?.url || {},
            subtitle: dataMedia?.['3_ONBOARDING_VIDEO']?.subtitle || {},
            onClose: () => {
              setTimeout(() => {
                updateProfile({ isWatchedTrainingVideo: true });
              }, 150);
              dispatch(redux.actionContainer?.hideVideoModal());
            },
          }),
        );
        return;
      }

      //3. if contract is not signed
      if (
        userProfile?.hasContractsToSign &&
        userProfile?.isFirstResetPassword &&
        userProfile?.isWatchedTrainingVideo
      ) {
        dispatch(
          redux.actionContainer.showConfirmModal({
            isShowCloseButton: false,
            isBackdropPress: false,
            title: t('SIGN_CONTRACT'),
            description: t('PLEASE_SIGN_CONTRACT_TO_START_WORKING'),
            confirmText: t('CONFIRM'),
            cancelText: t('CANCEL'),
            isShowIcon: true,
            onCancel: () => {
              dispatch(redux.actionContainer?.hideConfirmModal());
              setTimeout(() => {
                dispatch(redux.actionContainer.logout());
              }, 350);
            },
            onConfirm: () => {
              dispatch(redux.actionContainer?.hideConfirmModal());
              setTimeout(() => {
                navigationRef.navigate(SCREEN_ENUM.ContractScreen);
              }, 350);
            },
          }),
        );
        return;
      }

      //4. Check training session
      if (
        !userProfile?.isFinishedTraining &&
        userProfile?.isFirstResetPassword &&
        userProfile?.isWatchedTrainingVideo &&
        !userProfile?.hasContractsToSign
      ) {
        dispatch(
          redux.actionContainer.showConfirmModal({
            isShowCloseButton: false,
            isBackdropPress: false,
            isShowIcon: true,
            title: t('TRAINING_TEST'),
            description: t('PLEASE_PASS_TRAINING_TO_START_WORKING'),
            confirmText: t('COME_SEE'),
            cancelText: t('CANCEL'),
            onCancel: () => {
              dispatch(redux.actionContainer?.hideConfirmModal());
              setTimeout(() => {
                dispatch(redux.actionContainer.logout());
              }, 350);
            },
            onConfirm: () => {
              dispatch(redux.actionContainer?.hideConfirmModal());
              // navigationRef.navigate(SCREEN_ENUM.TrainingTestScreen);
            },
          }),
        );
        return;
      }

      // 5. Check due video
      if (
        (userProfile?.isDueTraining || userProfile?.needWatchVideoNow) &&
        userProfile?.isFirstResetPassword &&
        userProfile?.isWatchedTrainingVideo &&
        !userProfile?.hasContractsToSign &&
        userProfile?.isFinishedTraining &&
        !isShowDueVideo
      ) {
        dispatch(
          redux.actionContainer.showCustomModal({
            isBackdropPress: false,
            animationIn: 'zoomIn',
            animationOut: 'zoomOut',
            animationInTiming: 400,
            animationOutTiming: 400,
            backdropOpacity: 0.2,
            children: React.createElement(DueVideo),
          }),
        );
        return;
      }

      if (
        !userProfile?.isDueTraining &&
        !userProfile?.needWatchVideoNow &&
        userProfile?.isFirstResetPassword &&
        userProfile?.isWatchedTrainingVideo &&
        !userProfile?.hasContractsToSign &&
        userProfile?.isFinishedTraining &&
        importantJobs?.totalJobs > 0
      ) {
        dispatch(
          redux.actionContainer.showCustomModal({
            isBackdropPress: false,
            animationIn: 'zoomIn',
            animationOut: 'zoomOut',
            animationInTiming: 400,
            animationOutTiming: 400,
            backdropOpacity: 0.2,
            children: React.createElement(ImportantThings),
          }),
        );
      }
    } catch (error) {
      logger('error fetchStatusAccount', error);
    }
  }, [
    userProfile,
    importantJobs?.isLoading,
    importantJobs?.totalJobs,
    myJobsToday?.isLoading,
    dispatch,
    t,
    dataMedia,
    isShowDueVideo,
  ]);

  useEffect(() => {
    fetchGetMyTodayJobs();
    fetchGetImportantJobs();
  }, [fetchGetMyTodayJobs, fetchGetImportantJobs]);

  useFocusEffect(
    useCallback(() => {
      fetchStatusAccount();
    }, [fetchStatusAccount]),
  );
};
