import React from 'react';
import { View } from 'react-native';
import { Button, HeaderBase, SCREEN_WIDTH, Text } from 'tradewize-component';
import { colors } from '../../../../theme';
import { getTimePeriod } from '../../../../utils';
import { nature, new_user, night, sea, sunny } from '../../../../assets/images';
import { styles } from './styles';
import FastImage from 'react-native-fast-image';
import { useAppTranslation } from '../../../../hooks/useAppTranslation.ts';
import { useSelector } from 'react-redux';
import { NavigationService } from '../../../../services/NavigationService';
import { IconBell } from '../../../../assets/svg';
import { SvgXml } from 'react-native-svg';

const HeaderComponent = () => {
  const { t } = useAppTranslation();
  const userProfile = useSelector((state: any) => state.auth.userProfile);
  const timePeriod = getTimePeriod();

  const timePeriodIcon = () => {
    switch (timePeriod) {
      case 'morning':
        return sunny;
      case 'afternoon':
        return sea;
      case 'evening':
        return night;
      case 'night':
        return nature;
      default:
        return sunny;
    }
  };

  return (
    <HeaderBase
      leftActions={[
        {
          icon: (
            <Button
              variant="ghost"
              onPress={() => NavigationService.navigate('profileScreen')}
              style={styles.btnHeader}
            >
              <FastImage
                source={
                  userProfile?.photoUrl
                    ? { uri: userProfile?.photoUrl }
                    : new_user
                }
                style={styles.imgHeader}
                resizeMode="contain"
              />
            </Button>
          ),
          onPress: () => {},
        },
      ]}
      title={
        <View style={styles.userInfo}>
          <FastImage source={timePeriodIcon()} style={styles.timePeriodIcon} />
          <Text style={styles.txtWelcome}>
            {t(`GOOD_${timePeriod.toUpperCase()}`)}, {userProfile?.firstName}{' '}
            {userProfile?.lastName},
          </Text>
        </View>
      }
      rightActions={[
        {
          icon: (
            <Button variant="ghost" style={styles.btnBell}>
              <SvgXml
                xml={IconBell(colors.white)}
                width={SCREEN_WIDTH * 0.06}
                height={SCREEN_WIDTH * 0.06}
              />
            </Button>
          ),
        },
      ]}
      backgroundColor={colors.mainColorPrimary}
    />
  );
};

export default HeaderComponent;
